.summary-items {
  border-left: 1px solid var(--hrColor);
}
.summary-items:nth-of-type(1) {
  border-left: 0px solid var(--hrColor);
}
.summary-items > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.summary-items > div > p:nth-of-type(1) {
  font-size: 1rem;
  font-weight: bold;
  color: #095f62;
}
.summary-items > div > p:nth-of-type(2) {
  display: flex;
  align-items: center;
  justify-content: center;
}
.summary-items > div > p:nth-of-type(3) {
  text-transform: uppercase;
  color: darkgray;
  font-size: 0.75rem;
}
.summary-items > div > p:nth-of-type(4) {
  border-bottom: 1px solid var(--hrColor);
}
.summary-items > div > p:nth-of-type(2) img {
  width: 50%;
}
@media only screen and (max-width: 480px) {
  .summary-items {
    padding: 1rem;
    border-left: none;
  }
  .summary-items > div > p:nth-of-type(4) {
    border-bottom: none;
  }
  .summary-items > div > p:nth-of-type(3) {
    font-size: 11px;
    text-transform: capitalize;
  }
  .summary-items:nth-of-type(1) {
    border-bottom: 1px solid var(--hrColor);
    border-right: 1px solid var(--hrColor);
  }
  .summary-items:nth-of-type(2) {
    border-bottom: 1px solid var(--hrColor);
  }
  .summary-items:nth-of-type(3) {
    border-right: 1px solid var(--hrColor);
  }
}
@media only screen and (max-width: 1024px) {
  .summary-items {
    padding: 1.5rem;
    border-left: none;
    font-size: 1.75rem;
  }
  .summary-items > div > p:nth-of-type(1) {
    font-size: 1.5rem;
  }
  .summary-items > div > p:nth-of-type(4) {
    border-bottom: none;
  }
  .summary-items > div > p:nth-of-type(3) {
    font-size: 1rem;
    text-transform: capitalize;
  }
  .summary-items:nth-of-type(1) {
    border-bottom: 1px solid var(--hrColor);
    border-right: 1px solid var(--hrColor);
  }
  .summary-items:nth-of-type(2) {
    border-bottom: 1px solid var(--hrColor);
  }
  .summary-items:nth-of-type(3) {
    border-right: 1px solid var(--hrColor);
  }
}
@media only screen and (max-width: 767px) {
  .summary-items {
    font-size: 1.15rem;
  }
}
